	module.exports = {"data":{"site":{"siteMetadata":{"title":"Alex's Blog","author":"Alex J.Y."}},"markdownRemark":{"id":"/home/travis/build/jinyiabc/jinyiabc.github.io/src/pages/antihumanism/index.md absPath of file >>> MarkdownRemark","html":"<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/money-116e975540c5b6ea7f45ae88ee11403c-6164c.jpeg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 525px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 66.66666666666666%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"money\"\n        title=\"\"\n        src=\"/static/money-116e975540c5b6ea7f45ae88ee11403c-6164c.jpeg\"\n        srcset=\"/static/money-116e975540c5b6ea7f45ae88ee11403c-020a3.jpeg 148w,\n/static/money-116e975540c5b6ea7f45ae88ee11403c-85e76.jpeg 295w,\n/static/money-116e975540c5b6ea7f45ae88ee11403c-6164c.jpeg 525w\"\n        sizes=\"(max-width: 525px) 100vw, 525px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>In social theory and philosophy, <a href=\"https://en.wikipedia.org/wiki/Antihumanism\">antihumanism</a> (or anti-humanism) is a theory that is critical of traditional humanism and traditional ideas about humanity and the human condition. Central to antihumanism is the view that concepts of “human nature”, “man”, or “humanity” should be rejected as historically relative or metaphysical.</p>\n<p><a href=\"https://en.wikipedia.org/wiki/Human_nature\">Human nature</a> refers to the distinguishing characteristics—including ways of thinking, feeling, and acting—which humans tend to have naturally.</p>\n<p>The questions of whether there truly are fixed characteristics, what these natural characteristics are, and what causes them are among the oldest and most important questions in philosophy and science. The concept of human nature is traditionally contrasted not only with unusual human characteristics, but also with characteristics which are derived from specific cultures, and upbringings. The <a href=\"https://en.wikipedia.org/wiki/Nature_versus_nurture\">nature versus nurture</a> debate is a well-known modern discussion about human nature in the natural science.</p>\n<p>These questions have particularly important implications in economy, ethics, politics, and theology. <strong>This is partly because human nature can be regarded as both a source of norms of conduct or ways of life, as well as presenting obstacles or constraints on living a good life.</strong> The complex implications of such questions are also dealt with in art and literature, the question of what it is to be human.</p>","frontmatter":{"title":"Antihumanism","date":"April 29, 2018"}}},"pathContext":{"slug":"/antihumanism/","previous":{"fields":{"slug":"/new-blog/"},"frontmatter":{"title":"Hello New Blog"}},"next":{"fields":{"slug":"/tech-analysis/"},"frontmatter":{"title":"技术分析-学习笔记"}}}}

